import {noticeList} from "../../../api/user.js"
export default{
	data(){
		return {
			id:"",// car id
			list:[],
			isShowLoading:true,
			isLoadMore:true,
			page:1,
			scrollNodata:false
		}
	},
	onLoad(opts) {
		uni.hideShareMenu() 
		this.id = opts.carId
		this.getNoticeList()
	},
	onReachBottom() {
		this.getNoticeList()
	},
	methods:{
		getNoticeList(){
			if(!this.isLoadMore) return 
			this.isLoadMore = false
			noticeList({
				data:{
					car_id:this.id,
					page_no:this.page,
					page_size:20
				}
			}).then(res=>{
				if(res.data.code == 0) {
					let list =  res.data.result.list;
					if(list.length == 20) {
						this.isLoadMore = true;
						this.page++;
					}
					if (list.length < 20) {
						this.isShowLoading = false
					}
					if (list.length < 20 && this.page > 1) {
						this.scrollNodata = true
					}
					this.list = [...this.list,...list]
				}
			})
		}
	}
}
